import styled from 'styled-components';

const Span = styled.span`
  width: 70%;
  margin: 0 5px;
  color: #afafaf;
  white-space: pre;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  overflow: hidden;
`;

export default Span;



