import styled from 'styled-components';

const Input = styled.input`
  width: 150px;
  height: 40px;
  margin-right: 20px;
  padding: 0 4px 0 8px;
  vertical-align: middle;
  color: #222;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 3px;
`;

export default Input;



